import { gql } from "@apollo/client";

export const ALL_FOODS = gql `
    query allFoods {
        allFoods {
            id
            name
            time
            day
        }
    }
`;

export const CREATE_OR_UPDATE_FOOD = gql `
    mutation createOrUpdateFood (
        $day: String
        $name: String
        $time: Time
    ){
        createOrUpdateFood (
            day: $day
            name: $name
            time: $time
        ) {
            food {
                id
            }
        }
    }
`;

