import React from 'react';
import { Form, Input, message, Select, Spin } from 'antd';
import IntlMessage from 'components/util-components/IntlMessage';
import { useMutation } from '@apollo/client';
import { ALL_FOODS, CREATE_OR_UPDATE_FOOD } from 'graphql/food';

const { Option } = Select

function FoodForm ({ closeModal }) {

    const [form] = Form.useForm();

    const times = ['09:30', '11:00', '12:30', '16:30', '17:30']
    const days = [
        {
            mn: 'Даваа',
            en: 'Monday'
        },
        {
            mn: 'Мягмар',
            en: 'Tuesday'
        },
        {
            mn: 'Лхагва',
            en: 'Wednesday'
        },
        {
            mn: 'Пүрэв',
            en: 'Thursday'
        },
        {
            mn: 'Баасан',
            en: 'Friday'
        }
    ]

    const [fetchMutation, { loading: createLoading }] = useMutation(CREATE_OR_UPDATE_FOOD, {
        refetchQueries: [{
            query: ALL_FOODS
        }],
        onCompleted: data => {
            closeModal();
            form.resetFields();
            message.success("Амжилттай хадгаллаа")
        }
    })
    
    const onFinish = values => {
		fetchMutation({ variables: values })
    };

    return (
        <Spin spinning={createLoading} tip="Ачааллаж байна...">
            <Form  
                id="form"
                layout={'vertical'}
                form={form}
                name="form" 
                onFinish={onFinish}
            >
                <Form.Item name="name" label={<IntlMessage id="name" />} rules={[
                    {
                        required: true,
                        message: <IntlMessage id="form.required" />
                    }
                ]}>
                    <Input />
                </Form.Item>
                <Form.Item name="day" label={<IntlMessage id="day" />} rules={[
                    {
                        required: true,
                        message: <IntlMessage id="form.required" />
                    }
                ]}>
					<Select>
						{ days.map((item, index) => (
							<Option key={index} value={item.en} >{item.mn}</Option>
						))}
					</Select>
                </Form.Item>
                <Form.Item name="time" label={<IntlMessage id="time" />} rules={[
                    {
                        required: true,
                        message: <IntlMessage id="form.required" />
                    }
                ]}>
					<Select>
						{ times.map((item, index) => (
							<Option key={index} value={item+':00'} >{item}</Option>
						))}
					</Select>
                </Form.Item>
            </Form>
        </Spin>
    );
};

export default FoodForm