import React, { useRef } from 'react'
import { useQuery } from '@apollo/client'
import { Card } from 'antd'
import { PlusCircleOutlined } from '@ant-design/icons';
import { ALL_FOODS } from 'graphql/food'
import FoodForm from './form'
import FormModal from 'components/shared-components/FormModal'
import moment from 'moment';
import Loading from 'components/shared-components/Loading';
import { CheckPer } from 'hooks/checkPermission';


const Food = () => {

    const permissions = {
        change_food: CheckPer('change_food')
    }

    const modalRef = useRef();

    function handleCancel() {
        modalRef.current.handleCancel()
    }

    function handleOpen() {
        modalRef.current.handleOpen()
    }

    const { data, loading } = useQuery(ALL_FOODS)

    const times = ['09:30', '11:00', '12:30', '16:30', '17:30']
    const days = ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday']

    function findArrayElementByDay(time, day) {
        return data.allFoods.find((element) => {
            return element.day === day && moment(time + ':00', "HH:mm:ss").format('hh:mm') === moment(element.time, "HH:mm:ss").format('hh:mm');
        })
    }

    if (loading) {
        return <Loading cover='content' />
    }

    return (
        <>
            {permissions.change_food &&
                <FormModal ref={modalRef}>
                    <FoodForm closeModal={handleCancel} />
                </FormModal>
            }
            <Card
                title="Хоолны цэс"
                extra={ permissions.change_food && <PlusCircleOutlined type='primary' onClick={handleOpen} />}
            >
                <div className="relative overflow-x-auto">
                    <table className="w-full text-sm text-left rtl:text-right text-gray-500 dark:text-gray-400">
                        <thead className="text-xs text-gray-700 uppercase bg-gray-50 dark:bg-gray-700 dark:text-gray-400">
                            <tr>
                                <th scope="col" className="px-6 py-3">
                                    Цаг
                                </th>
                                <th scope="col" className="px-6 py-3">
                                    Даваа
                                </th>
                                <th scope="col" className="px-6 py-3">
                                    Мягмар
                                </th>
                                <th scope="col" className="px-6 py-3">
                                    Лхагва
                                </th>
                                <th scope="col" className="px-6 py-3">
                                    Пүрэв
                                </th>
                                <th scope="col" className="px-6 py-3">
                                    Баасан
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            {times.map(time => (
                                <tr key={time} className="bg-white border-b dark:bg-gray-800 dark:border-gray-700">
                                    <th scope="row" className="px-6 py-4 font-medium text-gray-900 whitespace-nowrap dark:text-white">
                                        {time}
                                    </th>
                                    {days.map(day => (
                                        <td key={day} className="px-6 py-4">
                                            {findArrayElementByDay(time, day)?.name}
                                        </td>
                                    ))}
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            </Card>
        </>
    )
}

export default Food